html {
  background: rgb(51, 102, 51);
}

.App {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}


.square {
  background: linear-gradient(orange, yellow);
}

.square-1 {
  background: linear-gradient(pink, lightblue);
}

.square, .square-1 {
  height: 100px;
  width: 100px;
  border-radius: 20px;
  margin: 10vh auto;
}